import menuItems from "./menuItems"; // Asegúrate de que esta ruta sea correcta
import MenuItem from "./MenuItem"; // Asegúrate de que esta ruta sea correcta

const Menu = () => {
  return (
    <nav className="p-4 w-full h-16">
      <ul className="flex flex-row gap-0 absolute">
        {menuItems.map((item, index) => (
          <MenuItem
            key={item.href || index} // Usar algo único como key
            item={item}
          />
        ))}
      </ul>
    </nav>
  );
};

export default Menu;
