import Link from "next/link";
import "./menu.css";

type itemData = {
  label: string;
  href?: string;
  children?: { label: string; href: string }[];
};

const MenuItem = ({ item }: { item: itemData }) => {
  const { label, href, children } = item;

  return (
    <nav className="menu-principal">
      <ul className="menu-superior">
        <li className="activo">
          <a href="#">Dashboard</a>
        </li>
        <li>
          <a href="#">Inicio</a>
        </li>
        <li className="con-desplegable">
          <a href="#">Perfil</a>
          <ul className="menu-desplegable">
            <li className="activo-desplegable">
              <a href="#">Inicio</a>
            </li>
            <li>
              <a href="#">Dashboard</a>
            </li>
            <li>
              <a href="#">Perfil</a>
            </li>
          </ul>
        </li>
      </ul>
    </nav>
  );
};
export default MenuItem;
